<template>
  <div class="sidebar-wrapper" :class="{mobileShow, specialSidebar, wide: isCollapsed}">
    <div class="opacity" @click="mobileShow = false"/>
    <div class="sidebar" :class="{wide: isCollapsed}">
      <el-menu router class="second" :class="{wide: isCollapsed}">
        <component :is="sidebarC"/>
      </el-menu>
      <el-menu router class="first" :class="{collapsed: isCollapsed}">
        <div class="nav-container" :class="{collapsed: isCollapsed}">
          <router-link to="/" id="home">
            <div class="logo" :class="{collapsed: isCollapsed}">
              <ii-logo-img class="logo-img"/>
              <ii-logo-text class="logo-text"/>
            </div>
          </router-link>
          <Modal v-model="dialogVisible"/>
          <el-menu-item @click="dialogVisible = true">
            <ii-create/>
            <span>Create</span>
          </el-menu-item>
          <router-link to="/campaigns" class-active="active">
            <!-- :class="{'active': true }" -->
            <el-menu-item>
              <ii-campaigns/>
              <span>Campaigns</span>
            </el-menu-item>
          </router-link>
          <router-link to="/settings" class-active="active">
            <el-menu-item>
              <ii-settings/>
              <span>Settings</span>
            </el-menu-item>
          </router-link>
          <router-link to="/contest" class-active="active">
            <el-menu-item>
              <ii-contest/>
              <span>Contests</span>
            </el-menu-item>
          </router-link>
          <h3 class="nav-section"/>
          <a href="http://training.iglooapp.com/" target="_blank">
            <el-menu-item>
              <ii-tutorial/>
              <span>Tutorials</span>
            </el-menu-item>
          </a>
          <a href="http://sambakkersupport.com/" target="_blank">
            <el-menu-item>
              <ii-support/>
              <span>Support</span>
            </el-menu-item>
          </a>
        </div>
        <div class="upgrades" :class="{collapsed: isCollapsed}">
          <div class="upgrade upgrade-text title">Upgrades</div>
          <div class="upgrade">
            <ii-all-access/>
            <span class="upgrade-text">
              All Access Pass
            </span>
          </div>
          <div class="upgrade">
            <ii-domains/>
            <span class="upgrade-text">
              + 20 Domains
            </span>
          </div>
          <div class="upgrade">
            <ii-bonuses/>
            <span class="upgrade-text">
              Bonuses
            </span>
          </div>
        </div>
      </el-menu>
    </div>
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import CampaignSpecialSidebar from "@/components/CampaignSpecialSidebar";

export default {
  components: {
    Modal,
  },
  data: () => ({
    specialSidebar: false,
    dialogVisible: false,
    isCollapsed: false,
    mobileShow: false,
    sidebarC: CampaignSpecialSidebar,
  }),
  watch: {
    '$route': {
      immediate: true,
      deep: true,
      handler(val) {
        this.isCollapsed = !!val.meta.sidebar;
        this.specialSidebar = !!val.meta.specialSidebar;

        if (typeof (val.meta.sidebar) != "boolean") {
          this.sidebarC = val.meta.sidebar;
        } else {
          this.sidebarC = CampaignSpecialSidebar;
        }
      }
    }
  },
  methods: {
    openSidebar() {
      this.mobileShow = !this.mobileShow;
    }
  },
  created() {
    this.$bus.$on('open-sidebar', this.openSidebar);
  }
}
</script>

