  background-color: black;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.4s;
}

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
