      }

      &.collapsed {
        margin-left: 13px;

        .logo-text {
          transition: all ease 1s;
          margin-left: 50px;
          opacity: 0;
        }
      }

      svg {

      }
    }
  }


  &-wrapper {
    width: 220px;
    display: flex;

    &.wide {
      width: 320px;
    }
  }

  .el-menu {
    overflow-y: auto;
  }

  //  .el-button {
  //    width: 100%;
