  //
  //    &#import-button {
  //      color: #1b4000;
  //    }
  //  }
}

.campaign {
  height: 57px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: $clr-txt-nav;
  transition: all ease 0.3s;
}

