.first {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 265px;
  height: 100%;
  transition: all 1s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .upgrades {
    width: 186px;
    height: 210px;
    min-height: 210px;
    background: url("~@/assets/upg-gradient.svg");
    padding: 18px 25px;
    border-radius: 8px;
    margin-bottom: 16px;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    transition: all ease 1s;

    padding-top: 76px;

    .upgrade {
      // TODO refactor
      color: white;
      display: flex;
      align-items: center;
      overflow: hidden;
      transition: all ease 1s;
      position: relative;


      left: 0px;

      &-text {
        opacity: 1;
        white-space: nowrap;
        position: absolute;
        left: 30px;
