      }
    }

    &.collapsed {
      padding: 40px 0;
      margin-bottom: 0;
      border-radius: 0;

      svg {
        margin-right: 0;
      }

      .upgrade {
        //justify-content: center;

        left: 26px;

        &-text {
          opacity: 0;
          position: absolute;
        }

        &.title {
          opacity: 0;
          left: -120px;
        }
      }
    }
  }
}

.el-menu {
  width: 220px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  a {
    text-decoration: none !important;
    text-underline: none !important;
  }

  &-item {
    height: 30px;
    margin-bottom: 6px;
