    //&:hover, &.is-active, .active {
    //  color: white;
    //}

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  svg {
    margin-right: 10px;
    height: 20px;
  }

  .nav-container {
    background-color: white;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;
    width: 320px;

    &-dark {
      transition: all 1s ease;
      background-color: white;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: black;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.second {
  padding-left: 74px;
  background-color: white;
  transition: all 0.4s ease;

  &.wide {
    width: 320px !important;
  }

  .nav-container, .nav-container-dark {
    background: white;
  }
}

.collapsed {
  width: 74px !important;

  .nav-section {
    overflow: hidden;
