  }

  .el-menu-item {
    padding-left: 27px !important;

    svg {
      transition: all ease 0.2s;
      margin: 0;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    span {
      opacity: 0;
    }
  }
}

//
@media (max-width: $media-tablet) {
  .specialSidebar {
    .sidebar {
      left: -320px;
      width: 0px !important;
    }

    &.sidebar-wrapper {
      width: 0px !important;
    }

    &.mobileShow {
      .sidebar {
        position: absolute;
        top: 0px;
        left: 0;
        width: 265px;
        bottom: 0;
        height: unset;

        &.wide {
          width: 320px !important;
        }
      }

      .opacity {
        opacity: 0.8;
        pointer-events: auto;
      }
    }
  }
}

@media (max-width: $media-mobile) {
  .sidebar {
    left: -320px;
    width: 0px !important;
  }

  .sidebar-wrapper {
    width: 0px !important;
  }

  .mobileShow {
    .sidebar {
      position: absolute;
      top: 0px;
      left: 0;
      width: 265px;
      bottom: 0;
      height: unset;

      &.wide {
        width: 320px !important;
      }
    }

    .opacity {
      opacity: 0.8;
      pointer-events: auto;
    }
  }
}

